import React from 'react';
import HistoryItem from './HistoryItem';

function HistoryTeam({ whichTeam, data }) {
  const { teamName, logo, brandColor, history } = data;

  // Populate history components.
  const historyComponents = [];
  // If there are more than 5 history items, take the last 5.
  const limit = history.length > 5 ? history.length - 5 : 0;
  // Looping from the last element.
  for (let i = history.length - 1; i >= limit; i--) {
    const { name, jerseyNumber } = history[i].player;

    historyComponents.push(
      <HistoryItem
        key={i}
        name={name}
        jerseyNumber={jerseyNumber}
        change={history[i].change}
      />
    );
  }

  return (
    <section
      className={whichTeam === '1' ? 'history__team-one' : 'history__team-two'}
    >
      <header
        className="history__header"
        style={{ backgroundColor: brandColor }}
      >
        <div className="logo">
          <img src={`./static/images/${logo}`} alt={`${teamName} logo`} />
        </div>
        <div className="team-name">{teamName}</div>
      </header>

      <section className="history__list">{historyComponents}</section>
    </section>
  );
}

export default HistoryTeam;
