import React from 'react';

function Player({ player, team, handleScore }) {
  const { name, jerseyNumber, image, score } = player;

  return (
    <div
      className="player"
      data-team={team === '1' ? 'teamOne' : 'teamTwo'}
      data-player-number={jerseyNumber}
      onClick={handleScore}
    >
      <div className="player__number">{jerseyNumber}</div>
      <div className="player__picture">
        <img src={`./static/images/${image}`} alt={name} />
      </div>
      <div className="player__name">{name}</div>
      <div className="player__score">
        <div className="score__total">{score}</div>

        <button
          className="score__changer score__addition score__addition--plus-one"
          data-value="1"
        >
          +1
        </button>
        <button
          className="score__changer score__addition score__addition--plus-two"
          data-value="2"
        >
          +2
        </button>
        <button
          className="score__changer score__addition score__addition--plus-three"
          data-value="3"
        >
          +3
        </button>

        <button
          className="score__changer score__subtraction score__subtraction--minus-one"
          data-value="1"
        >
          -1
        </button>
        <button
          className="score__changer score__subtraction score__subtraction--minus-two"
          data-value="2"
        >
          -2
        </button>
        <button
          className="score__changer score__subtraction score__subtraction--minus-three"
          data-value="3"
        >
          -3
        </button>
      </div>
    </div>
  );
}

export default Player;
