import React from 'react';
import Player from './Player';

function Team({ whichTeam, data, handleScore }) {
  const { teamName, logo, logoTransparent, brandColor, players, score } = data;

  let playerList = players.map(player => (
    <Player
      key={player.id}
      player={player}
      team={whichTeam}
      handleScore={handleScore}
    />
  ));

  return (
    <section
      className={
        whichTeam === '1' ? 'scoreboard__team-one' : 'scoreboard__team-two'
      }
    >
      <header
        className="scoreboard__header"
        style={{
          backgroundColor: `${brandColor}`,
          backgroundImage: `url('./static/images/${logoTransparent}')`
        }}
      >
        <div className="logo">
          <img src={`./static/images/${logo}`} alt={`${teamName} logo`} />
        </div>
        <div className="team-name">{teamName}</div>
        <div className="score">{score}</div>
      </header>

      <section className="scoreboard__players">{playerList}</section>
    </section>
  );
}

export default Team;
