import React from 'react';

function HistoryItem({ name, jerseyNumber, change }) {
  let changeColor = /\+\d/.test(change) ? 'addition' : 'subtraction';

  return (
    <div className="item">
      <div className="item__jersey-number">{jerseyNumber}</div>
      <div className="item__player-name">{name}</div>
      <div className={'item__score-change ' + changeColor}>{change}</div>
    </div>
  );
}

export default HistoryItem;
