import React from 'react';
import Scoreboard from './components/Scoreboard';
import History from './components/History';

class App extends React.Component {
  constructor() {
    super();
    this.state = { isLoading: true };
    this.handleScore = this.handleScore.bind(this);
  }

  componentDidMount() {
    this.setState({ isLoading: true });

    let lakers = getJSON('./data/lakers.json');
    let clippers = getJSON('./data/clippers.json');
    Promise.all([lakers, clippers]).then(teams => {
      // Initiating both team's and each player's score
      for (const team of teams) {
        team.score = 0;
        team.history = [];
        for (let player of team.players) {
          player.score = 0;
        }
      }
      this.setState({
        teamOne: teams[0],
        teamTwo: teams[1],
        isLoading: false
      });
    });
  }

  handleScore(event) {
    // If it's not a button, do nothing.
    if (event.target.tagName !== 'BUTTON') return;

    let btn = event.target;
    // The value of addition or subtraction.
    let value = Number(btn.dataset.value);

    let playerContainer = btn.closest('.player');
    // Team name according to state's key.
    let team = playerContainer.dataset.team;
    let playerNumber = Number(playerContainer.dataset.playerNumber);

    // If it's an addition.
    if (btn.className.includes('score__addition')) {
      this.setState(prevState => {
        let player = prevState[team].players.find(
          ({ jerseyNumber }) => jerseyNumber === playerNumber
        );

        // Update score.
        prevState[team].score += value;
        player.score += value;

        // Update history.
        prevState[team].history.push({
          player,
          change: `+${value}`
        });

        return prevState;
      });
    } else {
      this.setState(prevState => {
        let player = prevState[team].players.find(
          ({ jerseyNumber }) => jerseyNumber === playerNumber
        );

        // Update score.
        prevState[team].score -= value;
        player.score -= value;

        // Update history.
        prevState[team].history.push({
          player,
          change: `-${value}`
        });

        return prevState;
      });
    }
  }

  render() {
    return (
      <main className="container">
        {!this.state.isLoading && (
          <Scoreboard
            teamOne={this.state.teamOne}
            teamTwo={this.state.teamTwo}
            handleScore={this.handleScore}
          />
        )}

        {!this.state.isLoading && (
          <History teamOne={this.state.teamOne} teamTwo={this.state.teamTwo} />
        )}
      </main>
    );
  }
}

function getJSON(url) {
  return fetch(url).then(res => res.json());
}

export default App;
