import React from 'react';
import Team from './Team';

function Scoreboard({ teamOne, teamTwo, handleScore }) {
  return (
    <section className="scoreboard">
      <Team whichTeam="1" data={teamOne} handleScore={handleScore} />
      <Team whichTeam="2" data={teamTwo} handleScore={handleScore} />
    </section>
  );
}

export default Scoreboard;
