import React from 'react';
import HistoryTeam from './HistoryTeam';

function History({ teamOne, teamTwo }) {
  return (
    <section className="history">
      <header className="history__title">
        <h2>History</h2>
      </header>

      <section className="history__panel">
        <HistoryTeam whichTeam="1" data={teamOne} />
        <HistoryTeam whichTeam="2" data={teamTwo} />
      </section>
    </section>
  );
}

export default History;
